// Service Worker のバージョンとキャッシュする App Shell を定義する
const NAME = 'fertilizer-order';
const VERSION = '1.0.0';
const CACHE_NAME = `${NAME} ${VERSION}`;

// キャッシュするファイルを定義
const urlsToCache = [
    "fertilizer-order-detail.html",
    "fertilizer-order-detail.js",
    "fertilizer-order-detail.js.map",
    "fertilizer-order-end.html",
    "fertilizer-order-end.js",
    "fertilizer-order-end.js.map",
    "fertilizer-order-list.html",
    "fertilizer-order-list.js",
    "fertilizer-order-list.js.map",
    "./image/JA.png",
    "./image/icon-192.png",
    "./image/icon-512.png",
    "./lib/bootstrap-table-locale-all.min.js",
    "./lib/bootstrap-table.min.css",
    "./lib/bootstrap-table.min.js",
    "./lib/bootstrap.min.css",
    "./lib/bootstrap.min.js",
    "./lib/fontawesome-v5.0.6-all.js",
    "./lib/jquery-3.4.1.slim.min.js",
    "./lib/popper.min.js"
];

// Service Worker へファイルをインストール
self.addEventListener('install', function (event) {
    event.waitUntil(
        caches.open(CACHE_NAME)
            .then(function (cache) {
                console.log('Opened cache');
                return cache.addAll(urlsToCache);
            })
    );
});

// リクエストされたファイルが Service Worker にキャッシュされている場合
// キャッシュからレスポンスを返す
self.addEventListener('fetch', function (event) {
    if (event.request.cache === 'only-if-cached' && event.request.mode !== 'same-origin')
        return;
    event.respondWith(
        caches.match(event.request)
            .then(function (response) {
                if (response) {
                    return response;
                }
                return fetch(event.request);
            })
    );
});

// Cache Storage にキャッシュされているサービスワーカーのkeyに変更があった場合
// 新バージョンをインストール後、旧バージョンのキャッシュを削除する
// (このファイルでは CACHE_NAME をkeyの値とみなし、変更を検知している)
self.addEventListener('activate', event => {
    event.waitUntil(
        caches.keys().then(keys => Promise.all(
            keys.map(key => {
                console.log(`activate-key:${key}`);
                if (!CACHE_NAME.includes(key)) {
                    return caches.delete(key);
                }
            })
        )).then(() => {
            console.log(CACHE_NAME + "activated");
        })
    );
});
